<template>
  <div class="MySearch-container">
    <van-search v-model="Search"  placeholder="高山青 (Live版) 那英" shape="round"  @search="onSearch" />
    <div class="search_wrap" v-if="resultList.length ===0">
      <P class="hot_title">热门搜索</P>
      <div class="hot_name_wrap" >
        <span class="hot_item"
        v-for="(obj,index) in hotSong"
        :key="index"
        @click="fn(obj.first)"
        >{{ obj.first }}</span>
      </div>
    </div>

      <div class="search_wrap" v-else>
        <P class="hot_title">搜索结果</P>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad">
        <SongItem v-for="obj in resultList" :key="obj.id"
        :name="obj.name"
        :author="obj.ar[0].name"
        :id="obj.id"
        ></SongItem>
        </van-list>
      </div>

  </div>
</template>

<script>
import { getSearchHotSongAPI } from '@/API/SearchHotSong'
import { SearchSongAPI } from '@/API/SearchSongAPI'
import SongItem from '@/components/SongItem'
export default {
  components: {
    SongItem
  },
  data () {
    return {
      hotSong: [],
      Search: '',
      resultList: [],
      loading: false,
      finished: false,
      page: 1
    }
  },
  methods: {
    async getSearch () {
      const { data: res } = await getSearchHotSongAPI()

      this.hotSong = res.result.hots
    },
    async getSearchSong () {
      const { data: res } = await SearchSongAPI(this.Search, 20, this.page)
      this.resultList = res.result.songs
    },
    fn (e) {
      this.Search = e
      this.getSearchSong()
    },
    async onSearch () { // 按下回车搜索事件
      if (this.Search.length === 0) {
        this.resultList = []
        return
      }
      this.page = 1 // 当再次搜索需要把页码值重新赋值，不然会把之前页面值用在新搜索的歌手上
      const { data: res } = await SearchSongAPI(this.Search, 20, this.page)
      console.log(res.result.songs)
      if (!res.result.songs) { // 判断用户输入的是没有存在的歌曲，进行拦截，不存在的歌曲，songs返回的undefined;也可以 === undefined进行判断
        this.resultList = []
        return
      }
      this.resultList = res.result.songs
      this.finished = false // 解决当数据只有一页或者过短时，finished变为true 再搜索别的歌，也是finised为true;所以需要在触发事件后重新赋值
      this.loading = false // 搜索完，需要给loading状态重置，不然不刷新
    },
    async onLoad () { // 触底事件
      this.page++ // 触发事件，当前请求页面+1;
      const { data: res } = await SearchSongAPI(this.Search, 20, this.page)
      // 如果只是调用this.getSearchSong()这个方法，触发事件后新页面会覆盖旧页面。如果选择修改原来的发送请求，会发生搜索别的歌手，原来歌手的歌曲+新搜索的歌曲，所以需要再发一遍请求

      if (!res.result.songs) {
        this.finished = true
        return
      }
      // 最后一步:需要在获得数据前进行一个判断，遇到的问题 用长度为0判断一直报错，不能读长度，
      // 我console.log了一下当没数据时，并不会返回长度，所以就读不了这个长度，所以用!如果返回的是null则会return出去
      // console.log(res.result.songs.length)
      this.resultList = [...this.resultList, ...res.result.songs]
      this.loading = false
    }

  },
  created () {
    this.getSearch()
  }
}
</script>

<style scoped>
/* 搜索容器的样式 */
.search_wrap {
  padding: 0.266667rem;
}

/*热门搜索文字标题样式 */
.hot_title {
  font-size: 0.32rem;
  color: #666;
}

/* 热搜词_容器 */
.hot_name_wrap {
  margin: 0.266667rem 0;

}

/* 热搜词_样式 */
.hot_item {
  display: inline-block;
  height: 0.853333rem;
  margin-right: 0.213333rem;
  margin-bottom: 0.213333rem;
  padding: 0 0.373333rem;
  font-size: 0.373333rem;
  line-height: 0.853333rem;
  color: #333;
  border-color: #d3d4da;
  border-radius: 0.853333rem;
  border: 1px solid #d3d4da;
}
</style>
