import Vue from 'vue'
import VueRouter from 'vue-router'
import MyLayout from '@/views/Layout/MyLayout.vue'
import MyPlay from '@/views/Play/MyPlay.vue'
import MyHome from '@/views/Home/MyHome.vue'
import Mysearch from '@/views/Search/MySearch.vue'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/layout' },
  {
    path: '/layout',
    component: MyLayout,
    redirect: '/layout/home',
    children: [
      { path: 'home', component: MyHome, meta: { title: '首页' } },
      { path: 'search', component: Mysearch, meta: { title: '搜索' } } // meta 元信息，给当前路由绑定对象
    ]
  },
  { path: '/play', component: MyPlay }

]

const router = new VueRouter({
  routes
})

export default router
