import Vue from 'vue'
import App from './App.vue'
import router from './router'
import '@/mobile/flexible'
import '@/styles/reset.css'
import '@/assets/font/iconfont/Source_Han_Serif_SC_Light_Light.otf'
import { NavBar, Tabbar, TabbarItem, Col, Row, Image as VanImage, Cell, Icon, Search, List } from 'vant'

Vue.use(List)

