import request from '@/utils/baseUrl'

// 播放页 - 获取歌曲详情
export const getPlayAPI = function (ids) {
  return request.get('/song/detail', {
    params: {
      ids
    }
  })
}

// 播放页 - 获取歌词
export const getLyricAPI = function (id) {
  return request.get('/lyric', {
    params: {
      id
    }
  })
}
