<template>
<div class="MyHome-container">
<p class="title">推荐歌单</p>
<div>
  <van-row gutter="10">

  <van-col span="8" v-for="obj in recommendlist" :key="obj.id"><van-image
  width="100%"
  height="3rem"
  fit="contain"
  :src=obj.picUrl

/>
<P class="song_name">{{ obj.name }}</P>
</van-col>
</van-row>
</div>
<p class="title">最新音乐</p>

<SongItem v-for="obj in newSongList" :key="obj.id"
        :name="obj.name"
        :author="obj.song.artists[0].name "

        :id="obj.id"
        ></SongItem>
</div>
</template>

<script>
import { getRecsongAPI } from '@/API/RecsongAPI'
import { getNewSongAPI } from '@/API/NewsongAPI'
import SongItem from '@/components/SongItem'
export default {
  components: {
    SongItem
  },
  data () {
    return {
      recommendlist: [], // 推荐歌单
      newSongList: [] // 最新音乐
    }
  },
  methods: {
    async  AcRecsongList () {
      const { data: res } = await getRecsongAPI()
      this.recommendlist = res.result
    },
    async  getNewSong () {
      const { data: res } = await getNewSongAPI()
      this.newSongList = res.result
    }
  },
  created () {
    this.AcRecsongList()
    this.getNewSong()
  }
}
</script>

<style scoped>
/* 标题 */

.title {
  padding: 0.266667rem 0.24rem;
  margin: 0 0 0.24rem 0;
  background-color: #eee;
  color: rgb(212, 60, 51);
  font-size: 30px;

}
/* 推荐歌单 - 歌名 */
.song_name {
  font-size: 0.346667rem;
  padding: 0 0.08rem;
  margin-bottom: 0.266667rem;
  word-break: break-all;
  text-overflow: ellipsis;
  display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
  -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
  -webkit-line-clamp: 2; /** 显示的行数 **/
  overflow: hidden; /** 隐藏超出的内容 **/

}
</style>
