<template>

    <van-cell :title="name"  :label="author"  >
        <template #right-icon>
        <van-icon name="play-circle-o" size="0.6rem" @click="skipPlay"/>
        </template>
        </van-cell>

</template>

<script>

export default {
  props: {
    name: String, // 歌名
    id: Number,
    author: String // 作者

  },
  methods: {
    skipPlay () {
      this.$router.push({
        path: '/play',
        query: {
          id: this.id
        }
      })
    }
  }

}
</script>

